let baseUrl = ''
let imageServerUrl = 'http://image.cds.bakheet.cn'

if (process.env.NODE_ENV === 'production') {
  baseUrl = 'http://192.168.4.70:2222'
  imageServerUrl = 'http://192.168.4.62'
  // baseUrl = 'http://192.168.4.76:8222'
  // imageServerUrl = 'http://image.cds.bakheet.cn'
} else if (process.env.NODE_ENV === 'development') {
  baseUrl = 'http://192.168.4.70:2222'
} else if (process.env.NODE_ENV === 'testing') {
  baseUrl = 'http://192.168.4.70:8222'
  imageServerUrl = 'http://192.168.4.62'
}

export {
  baseUrl,
  imageServerUrl
}



